___CSS_LOADER_EXPORT___.push([module.id, `/* Background overlay */
.loading-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /*Ensure it appears above other content */
}

/* Loading container */
.loading-container {
  text-align: center;
  color: #00bcd4; 
}

/* Progress bar background */
.loading-bar-background {
  width: 300px;
  height: 20px;
  background: #1a1a1a;
  border: 2px solid #00bcd4;
  border-radius: 10px;
  overflow: hidden;
  margin: 10px auto;
  box-shadow: 0 0 15px #00bcd4;
}

/* Progress bar */
.loading-bar {
  height: 100%;
  background: linear-gradient(90deg, #00bcd4, #004d80);
  transition: width 0.3s ease-in-out;
}

/* Loading text */
.loading-text {
  font-family: 'Zilla Slab', serif;
  font-size: 18px;
  color: #00bcd4;
  text-shadow: 0 0 10px #00bcd4;
}
