___CSS_LOADER_EXPORT___.push([module.id, `/* The main container for the error screen */
.error-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #0d0f13;  /* Dark background */
  color: #00bcd4;  /* Glowing blue text */
  font-family: 'Orbitron', sans-serif;  /* Futuristic font */
  text-align: center;
  padding: 30px;
  box-sizing: border-box;
  text-shadow: 0 0 10px #00bcd4, 0 0 20px #00bcd4, 0 0 30px #00bcd4;
}

/* Title styling */
.error-screen h1 {
  font-size: 3em;  /* Larger title */
  margin-bottom: 20px;
  color: #00bcd4;
  text-shadow: 0 0 15px #00bcd4, 0 0 30px #00bcd4;
}

/* Paragraph text styling */
.error-screen p {
  font-size: 1.5em;  /* Slightly larger text */
  margin: 15px 0;
  color: #f8d7da;  /* Light error color */
  text-shadow: 0 0 8px #f8d7da, 0 0 15px #f8d7da;
}

